<template>
<div>
  <ul>
    <div class="flex_box">
      <span>工区名称：</span>
      <div class="flex_items" @click="toPage('EditName')">
        <p>{{name || '未设置'}}</p>
      </div>
    </div>
    <div class="flex_box">
      <span>签到间隔：</span>
      <div class="flex_items" @click="setTime">
        <p>{{time || '未设置'}}</p>
      </div>
    </div>
    <div class="flex_box">
      <span>工区位置：</span>
      <div class="flex_items" @click="toPage('Loaction')">
        <p>{{position || '未设置'}}</p>
      </div>
    </div>
    <div class="flex_box">
      <span>签到范围：</span>
      <div class="flex_items" @click="setRange">
        <p>{{range || '未设置'}}</p>
      </div>
    </div>
  </ul>
  <div class="btn_box">
      <mt-button type="primary" size="large" @click="save">保存</mt-button>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      name: '',
      time: '',
      position: '',
      range: ''
    }
  },
  mounted () {
  },
  methods: {
    save () {},
    toPage (pathName) {
      this.$router.push({name: pathName})
    },
    setTime () {
      this.$messagebox.prompt(null, '签到间隔', {
        inputPlaceholder: this.range,
        inputType: 'number'
      }).then(({ value, action }) => {
        console.log(value)
        console.log(action)
      })
    },
    setRange () {
      this.$messagebox.prompt(null, '签到范围', {
        inputPlaceholder: this.time,
        inputType: 'number'
      }).then(({ value, action }) => {
        console.log(value)
        console.log(action)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.flex_box {
  display: flex;
  padding: 15px;
  border-bottom: 1px solid #eee;
  span {
    font-weight: bold;
    width: 80px;
  }
}
.flex_items {
  flex: 1;
  width: calc(100% - 80px);
  position: relative;
  p {
    padding-right: 15px;
    float: right;
    &::after {
      content: " ";
      display: inline-block;
      height: 6px;
      width: 6px;
      border-width: 2px 2px 0 0;
      border-color: #1a1a1a;
      border-style: solid;
      -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
      transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
      position: relative;
      top: -2px;
      position: absolute;
      top: 50%;
      margin-top: -4px;
      right: 2px;
    }
  }
}
.btn_box {
  margin: 30px auto 0;
  width: 80%;
  text-align: center;
}
</style>



